@import "../../../../scss/core/functions/functions";
@import "../../../../scss/core/settings/settings";              // settings
@import "../../../../scss/core/mixins/mixins";
@import "../../../../scss/core/variables/variables";

.def-modal {
  height: 100%;
  overflow-y: auto;
  background-color: #fff;
  position: relative;
  width: 100%;
  &_padding {
    padding: 48px 24px;
    @include point-md {
      padding: 48px;
    }
  }
  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
    z-index: 1;
  }
  &__icon {
    display: block;
    width: 16px;
    height: 16px;
    fill: #757575;
  }
}
